import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import Aircon from '../views/Aircon'
import Login from '../views/Login'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'home',
        component: Home
    },
    {
        path: '/aircon',
        name: 'aircon',
        component: Aircon
    },
    {
        path: '/login',
        name: 'login',
        component: Login
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router